body {
  background-image: repeating-linear-gradient(
    to left top,
    rgba(218, 215, 237, 0.863),
    rgba(3, 3, 3, 0.7) 5px
  );
}
#ball_id {
  background-color: #a50707;
  border-radius: 50%;
  height: 100px;
  width: 100px;
  position: absolute;
  left: 1px;
  top: 1px;
  float: left;
  display: inline;
  box-sizing: border-box;
  padding-top: 35px;
  padding-left: 30px;
  color: white;
  border: 2px solid white;
}
@media only screen and (max-width: 600px) {
  #ball {
    height: 100px;
    width: 100px;
    padding-top: 35px;
    padding-left: 25px;
  }
}
